<template>
  <el-container style="height: 100%;">
    <!--头部-->
    <el-header class="header">
      <div class="header_left">
        <!--<div class="header_logo">宴</div>
        <span class="header_title">商家中心</span>-->
        <img class="header_logoImg" src="./../assets/logo.png" />
        <el-select v-model="storeChoose" placeholder="全部门店" size="small" class="header_store">
          <el-option
            v-for="item in stores"
            :key="item.value"
            :label="item.label"
            :value="item.value">
          </el-option>
        </el-select>
        <div class="siderBar_status">
          <p>已关店</p>
          <el-dropdown size="small"  trigger="click">
            <span class="el-dropdown-link">
              <img src="./../assets/menu.png" />
            </span>
            <el-dropdown-menu slot="dropdown" class="siderBar_dropdown_choose">
              <div>营业时间:10:00-20:00</div>
              <el-dropdown-item>
                <el-radio v-model="storeStatus" label="1">
                  <p>恢复营业</p>
                  <p>按营业时间自动开关店</p>
                </el-radio>
              </el-dropdown-item>
              <el-dropdown-item>
                <el-radio v-model="storeStatus" label="2">
                  <p>紧急关店</p>
                  <p>严重影响客户体验，请谨慎操作</p>
                </el-radio>
              </el-dropdown-item>
            </el-dropdown-menu>
          </el-dropdown>
          <div>到达营业时间会自动调整为营业中</div>
        </div>
        <el-input
          style="width:410px;margin-left:13px;"
          class="header_search"
          size="small"
          placeholder="输入取餐号、流水编号、订单号、手机号、地址"
          suffix-icon="el-icon-search"
          v-model="search">
        </el-input>
        <el-button size="small" style="margin-left:13px;color:#000;font-weight:bold">自动接单</el-button>
      </div>
      <div class="header_right">
        <el-dropdown size="small"  trigger="click"  @command="loginOut">
          <span class="el-dropdown-link">
            <img class="header_user" src="./../assets/logo.png" />
          </span>
          <el-dropdown-menu slot="dropdown" class="siderBar_dropdown">
            <el-dropdown-item>pk</el-dropdown-item>
            <el-dropdown-item divided command="login">退出登录</el-dropdown-item>
          </el-dropdown-menu>
        </el-dropdown>
        <!--<el-badge is-dot class="item header_notice">
          <img src="./../assets/notice.png" />
        </el-badge>-->
      </div>
    </el-header>
    <!--底部导航栏-->
    <el-container style="margin-top:5px;" class="body">
      <el-aside style="width:200px;" :style="{'height':siderHeight}">
        <!--现配-->
        <el-menu @select="selectMenu" :active-text-color="activeTextColor" :background-color="backgroundColor" :text-color="textColor" router :unique-opened="uniqueOpen" :default-active="$route.path" class="el-menu-vertical-demo">
          <template  v-for="(item , index) in siderbarList">
      　　  <el-submenu :index="item.path"  v-if="item.ischild == 'true'">
              <template slot="title">
                <!--<img :src="imgas" :alt="item.name" class="siderbar_img" />-->
                <span>{{item.name}}</span>
              </template>
              <div v-for="(itemChild , index) in item.child" class="siderBar_child">
            　　<el-submenu v-if="itemChild.ischild == 'true'" :index="itemChild.path" :key="index">
                  <template slot="title">
                    <!--<i class="el-icon-tickets"></i>-->
                    <span>{{itemChild.name}}</span>
                  </template>
            　　</el-submenu>
            　　<el-menu-item v-else :index="itemChild.path" :key="index">
                  <!--<i class="el-icon-tickets"></i>-->
                  <div v-if="itemChild.path == '/manager/orders'">
                    <span style="padding-left: 28px;">{{itemChild.name}}</span>
                    <span class="badge">9</span>
                  </div>
                  <div v-else>
                    <span>{{itemChild.name}}</span>
                  </div>
            　　</el-menu-item>
              </div>
            </el-submenu>   
  　　　　　 <el-menu-item :index="item.path" v-else>
              <!--<img :src="imgas" :alt="item.name" class="siderbar_img" />-->
              <span>{{item.name}}</span>
      　　  </el-menu-item>
          </template>
        </el-menu>
      </el-aside>
      <!--通配模块-->
      <transition name="fade"  mode="out-in">
        <router-view style="width:100%;min-width:880px;margin:0 12px;margin-right:0;overflow-y:auto;" :style="{'height':siderHeight}"></router-view>	
      </transition>
    </el-container>
  </el-container>
</template>

<script>
export default {
  data () {
    return {
      siderHeight:0,
      stores:[{
        label:'店铺1',
        value:1
      },{
        label:'店铺2',
        value:2
      },{
        label:'店铺3',
        value:3
      }],
      siderbarList:[],
      uniqueOpen:true,
      backgroundColor:'#fff',
      textColor:'#000',
      activeTextColor:'#fff',
      storeStatus:'1'
    }
  },
  methods:{
    // 通过跳转一个空页面再返回的方式来实现刷新当前页面数据的目的
    onRefresh () {
      this.$store.state.network = true      
      this.$router.replace('/manager/refresh')    
    },
    //退出登陆
    loginOut(val){
      // localStorage.setItem('userinfo',JSON.stringify(user));  设置local
      // localStorage.getItem('userinfo')  获取local
      if(val == 'login'){
        localStorage.removeItem('token');
        this.$router.push('/manager/login')
      }
    },
    //网络问题重新激活导航
    selectMenu(val){
      if(this.$store.state.network == false ){
        this.$store.state.network = true
      }
    }
  },
  computed: {
    storeChoose:{
      get:function(){
        return this.$store.getters.storesMessage
      },
      set:function(){

      }
    },
    search:{
      get:function(){
        return this.$store.getters.search
      },
      set:function(){

      }
    }, 
    network(){
      
    } 
  },
  components: {
    
  },
  created(){
    this.siderbarList = this.$store.state.siderBar.list
    this.siderHeight = (document.body.clientHeight - 75) + 'px'
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">
  .body > div::-webkit-scrollbar{
    /*滚动条整体样式*/
    width : 1px;  /*高宽分别对应横竖滚动条的尺寸*/
    height: 1px;
    background:#fff;
  } 
  .body > div::-webkit-scrollbar-thumb{
    /*滚动条里面小方块*/
    border-radius   : 10px;
    background-color: #eee;
  } 
  .body > div::-webkit-scrollbar-track{
    /*滚动条里面轨道*/
    background   : #eee;
    border-radius: 10px;
  } 

  .body /deep/ .el-aside::-webkit-scrollbar{
    /*滚动条整体样式*/
    width : 1px;  /*高宽分别对应横竖滚动条的尺寸*/
    height: 1px;
    background:#fff;
  } 
  .body /deep/ .el-aside::-webkit-scrollbar-thumb{
    /*滚动条里面小方块*/
    border-radius   : 10px;
    background-color: #eee;
  } 
  .body /deep/ .el-aside::-webkit-scrollbar-track{
    /*滚动条里面轨道*/
    background   : #eee;
    border-radius: 10px;
  } 
  .header{
    border-bottom:1px solid #cccccc;
    height:70px!important;
    display:flex;
    justify-content:space-between;
    align-items:center;
  }
  .header_left,.header_right{
    display:flex;
    align-items:center;
    justify-content:flex-start;
  }
  .header_title{
    font-size:14px;
    padding-left:12px;
  }
  .header_store{
    width:200px;
    margin-left:13px;
  }
  .header_logoImg{
    width:30px;
    height:30px;
    margin-right:37px;
  }
  .header_logo{
    border:1px solid #cccccc;
    border-radius:100%;
    font-size:18px;
    box-sizing:border-box;
    padding:10px 15px;
  }
  .header_search:focus{
    border-color:#cccccc;
  }
  .header_search .el-input__suffix,.header_search input::-webkit-input-placeholder,.header_search input{
    color:#000;
  }
  .header_user{
    width:22px;
    height:22px;
    padding:6px;
    border:1px solid #cccccc;
    border-radius:100%;
    cursor:pointer;
  }
  .header_notice{
    width:24px;
    height:24px;
    margin:0 30px;
    cursor:pointer;
  }
  .fade-enter-active, .fade-leave-active {
    transition: opacity .25s;
  }
  .fade-enter, .fade-leave-to /* .fade-leave-active below version 2.1.8 */ {
    opacity: 0;
  }
  .el-menu{
    border-right:0;
  }
  .el-aside ul li{
    min-width: auto!important;
    border:1px solid #cccccc;
    text-align:center;
  }
  .badge{
    display:inline-block;
    box-sizing:border-box;
    padding:5px 8px;
    border-radius:100%;
    background-color:red;
    color:#fff;
    line-height: 1;
  }
  .network{
    width:100%;
    display:flex;
    flex-direction:column;
    justify-content:flex-start;
    align-items:center;
    margin-top:20vh;
  }
  .network span{
    cursor:pointer;
    color:#409EFF
  }
  .siderBar_status{
    display:flex;
    justify-content:flex-start;
    align-items:center;
    border-left:1px solid #e6e6e6;
    height:40px;
    margin-left:40px;
  }
  .siderBar_status p{
    color:red;
    font-size:18px;
    padding-left:40px;
  }
  .siderBar_status img{
    width:24px;
    height:24px;
    cursor:pointer;
    margin:0 20px;
  }
  .siderBar_status div{
    color:#259B24;
    font-size:14px;
  }
  /*去掉导航栏侧边的上下图标*/
  .el-aside ul li i{
    display:none;
  }
  .siderBar_child li{
    background-color:#F0F0F0!important;
  }
  .el-aside ul li .el-submenu__title{
    display:flex;
    justify-content:center;
  }
  /*导航父级与子级颜色变化*/
  .is-active{
    background-color:#FF9800!important;
  }
  .siderBar_child .is-active{
    color:#FF9800!important;
  }

  .siderBar_dropdown li{
    text-align: center;
  }
  .siderBar_dropdown_choose{
    box-sizing:border-box;
    padding: 10px 20px;
  }
  .siderBar_dropdown_choose li{
    background-color:#e6e6e6;
    margin-top:10px;
  }
  .siderBar_dropdown_choose .el-radio__label{
    color:#000000;
  }
  .siderBar_dropdown_choose li label{
    display:flex;
    align-items:center;
    box-sizing:border-box;
    padding: 10px 0;
  }
  .siderBar_dropdown_choose li p:nth-child(2n){
    color:gray;
    padding-top:5px;
  }
  .siderBar_dropdown_choose .el-radio__input.is-checked+.el-radio__label{
    color:#000000;
  }
  .siderBar_dropdown_choose .el-radio__input.is-checked .el-radio__inner{
    border-color: #259B24;
    background: #259B24;
  }
</style>
